/* Reset some default styles and set a base font size */
body, h1, h2, p {
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Minimum height to cover the viewport */
}

/* Header styles */
header {
  background-color: #007BFF;
  color: #fff;
  padding: 1rem;
  text-align: center;
}

header h1 {
  font-size: 2rem;
}

nav ul {
  list-style: none;
}

nav ul li {
  display: inline;
  margin-right: 1rem;
}

nav ul li:last-child {
  margin-right: 0;
}

nav a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease;
}

nav a:hover {
  color: #0056b3;
}

/* Container styles */
.container {
  display: flex;
}

/* Sidebar styles */
.sidebar {
  width: 250px; /* Adjust the width as needed */
  background-color: #f4f4f4;
  padding: 1rem;
  border-right: 4px solid #007BFF; /* Add a border to separate the sidebar */
}

.sidebar h2 {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar li {
  margin-bottom: 0.5rem;
}

/* Main content styles */
.content {
  flex-grow: 1;
  padding: 20px; /* Add padding to separate content from sidebar */
}

.content h1 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.content p {
  font-size: 1rem;
  line-height: 1.4;
}

/* Footer styles */
footer {
  background-color: #007BFF;
  color: #fff;
  padding: 1rem;
  text-align: center;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
}
